import React from 'react';
import Layout from '../../../components/Layout';
import ModularBricks from './ModularBricks';

const title = 'Modular Bricks';

function action() {
  return {
    chunks: ['home'],
    title,
    component: (
      <Layout>
        <ModularBricks title={title} />
      </Layout>
    ),
  };
}

export default action;



