import React from 'react';
import Layout from '../../../components/Layout';
import MeetOurTeam from './MeetOurTeam';

const title = 'Meet Our Team';

function action() {
  return {
    chunks: ['home'],
    title,
    component: (
      <Layout>
        <MeetOurTeam title={title} />
      </Layout>
    ),
  };
}

export default action;



