import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import FontAwesome from 'react-fontawesome';
import s from './HamburgerMenu.css';
import Link from '../Link';

class HamburgerMenu extends React.Component {
  state = { isOpen: false };

  handleClick = event => {
    if (event.defaultPrevented === true) {
      return;
    }

    this.setState({
      isOpen: !this.state.isOpen,
    });
  };

  render() {
    const isOpen = this.state.isOpen;

    return (
      <div className={`${s.root}${isOpen ? ` ${s.open}` : ''}`}>
        <div
          className={s.drawer}
          onClick={this.handleClick}
          role="button"
          tabIndex="0"
        >
          <ul className={s.menu}>
            <li className="">
              <Link className={s.link} to="/" onClick={this.handleClick}>
                Home
              </Link>
            </li>
            <li className="">
              <Link className={s.link} to="#">
                About Us
              </Link>
              <ul className={s.subMenu}>
                <li>
                  <Link
                    to="/about-us/company-profile"
                    onClick={this.handleClick}
                  >
                    Company Profile
                  </Link>
                </li>
                <li>
                  <Link to="/about-us/meet-our-team" onClick={this.handleClick}>
                    Meet Our Team
                  </Link>
                </li>
                <li>
                  <Link
                    to="/about-us/bricks-manufacturing"
                    onClick={this.handleClick}
                  >
                    Bricks Manufacturing
                  </Link>
                </li>
                {/*
                <li>
                  <Link
                    to="/about-us/company-profile"
                    onClick={this.handleClick}
                  >
                    Jobs Opportunity
                  </Link>
                </li>
*/}
              </ul>
            </li>
            <li className="">
              <Link className={s.link} to="#">
                Products
              </Link>
              <ul className={s.subMenu}>
                <li>
                  <Link
                    to="/products/common-solid-bricks"
                    onClick={this.handleClick}
                  >
                    Common Solid Bricks
                  </Link>
                </li>
                <li>
                  <Link to="/products/common-blocks" onClick={this.handleClick}>
                    Common Blocks
                  </Link>
                </li>
                <li>
                  <Link
                    to="/products/modular-bricks"
                    onClick={this.handleClick}
                  >
                    Modular Bricks
                  </Link>
                </li>
                <li>
                  <Link to="/products/facing-bricks" onClick={this.handleClick}>
                    Facing Bricks
                  </Link>
                </li>
                <li>
                  <Link to="/products/brick-tiles" onClick={this.handleClick}>
                    Brick Tiles
                  </Link>
                </li>
              </ul>
            </li>
            <li className="">
              <Link className={s.link} to="#">
                Recent Projects
              </Link>
              <ul className={s.subMenu}>
                <li>
                  <Link to="/recent-projects" onClick={this.handleClick}>
                    Recent Projects
                  </Link>
                </li>
                <li>
                  <Link
                    to="/recent-projects/johor-bahru"
                    onClick={this.handleClick}
                  >
                    Johor Bahru
                  </Link>
                </li>
                <li>
                  <Link
                    to="/recent-projects/indah-putra"
                    onClick={this.handleClick}
                  >
                    Indah Putra
                  </Link>
                </li>
                <li>
                  <Link to="/recent-projects/tebrau" onClick={this.handleClick}>
                    Tebrau
                  </Link>
                </li>
                <li>
                  <Link
                    to="/recent-projects/plentong"
                    onClick={this.handleClick}
                  >
                    Plentong
                  </Link>
                </li>
              </ul>
            </li>
            <li className="">
              <Link
                className={s.link}
                to="/contact-us"
                onClick={this.handleClick}
              >
                Contact Us
              </Link>
            </li>
          </ul>
        </div>
        <button onClick={this.handleClick}>
          <FontAwesome name={`lg fa-navicon${isOpen ? ' fa-rotate-90' : ''}`} />
        </button>
      </div>
    );
  }
}

export default withStyles(s)(HamburgerMenu);



