import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from '../Sidebar.css';
import Link from '../../Link';

class SidebarProducts extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  render() {
    return (
      <div className={s.root} role="navigation">
        <ul className={s.menu}>
          <li
            className={
              this.props.title === 'Common Solid Bricks' ? s.current : ''
            }
          >
            <Link className={s.link} to="/products/common-solid-bricks">
              Common Solid Bricks
            </Link>
          </li>
          <li className={this.props.title === 'Common Blocks' ? s.current : ''}>
            <Link className={s.link} to="/products/common-blocks">
              Common Blocks
            </Link>
          </li>
          <li
            className={this.props.title === 'Modular Bricks' ? s.current : ''}
          >
            <Link className={s.link} to="/products/modular-bricks">
              Modular Bricks
            </Link>
          </li>
          <li className={this.props.title === 'Facing Bricks' ? s.current : ''}>
            <Link className={s.link} to="/products/facing-bricks">
              Facing Bricks
            </Link>
          </li>
          <li className={this.props.title === 'Brick Tiles' ? s.current : ''}>
            <Link className={s.link} to="/products/brick-tiles">
              Brick Tiles
            </Link>
          </li>
        </ul>
      </div>
    );
  }
}

export default withStyles(s)(SidebarProducts);



