import React from 'react';
import PropTypes from 'prop-types';
import Carousel from 'nuka-carousel';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import Breadcrumb from '../../../components/Breadcrumb';
import Sidebar from '../../../components/Sidebar/Products';
import s from '../Products.css';

class BrickTiles extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  render() {
    return (
      <div className={s.root}>
        <Breadcrumb title={this.props.title} />
        <div className={s.container}>
          <Sidebar title={this.props.title} />
          <div className={s.contents}>
            <h1>
              {this.props.title}
            </h1>
            <div className={s.carousel}>
              <Carousel
                autoplay
                initialSlideHeight={431}
                initialSlideWidth={655}
                wrapAround
              >
                <img
                  alt="Rockface Tiles 05K"
                  src="/images/products/MB_RF-05K.jpg"
                />
                <img
                  alt="Rockface Tiles 02K"
                  src="/images/products/MB_RF-02K.jpg"
                />
                <img
                  alt="Veneer Tile 04K"
                  src="/images/products/MB_WST-04K.jpg"
                />
                <img
                  alt="Veneer Tile 05K"
                  src="/images/products/MB_WST-05K.jpg"
                />
              </Carousel>
            </div>
            <p>...</p>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(BrickTiles);



