var _ref5 = _jsx('p', {}, void 0, 'Preparation consists of transforming the clay rock into plastic mouldable material by a process of grinding and mixing with water. A typical factory might have a Primary crusher, these are used to break down large lumps of rock to manageable size, which can then be fed to a Secondary crusher, for example Pan mill, where the clay is reduced in size further. Water can be added here or if it is a dry pan the clay is reduced to dust and water added later. Further crushing takes place through conveyor rollers reducing the clay particles to about 1-2mm.');

